* {
  box-sizing: border-box;
  /* background: whitesmoke; */
}

.app-bg .column {
  width: 20%;
  display: flex;
  flex-direction: column;
  /* justify-content: space-around; */
  align-items: center;
  /* justify-items: center; */
  /* justify-content: center; */
  /* margin-top: 300px; */
}
.column {
  margin: 20px;

}
.droppable-col {
  width: 100%;
  background-color: rgb(206, 68, 68);
  padding: 10px 10px 0 10px;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  /* margin: 10px; */
  box-shadow: 2px 2px 4px 0px #212121;

  /* justify-content: space-around !important; */
}

.item {
  margin-bottom: 10px;
  background-color: rgb(206, 68, 68);
  color: white;
  /* border-radius: 7px; */
  /* display: flex; */
  /* flex-direction: column; */
  border-left: 4px solid #212121;
  font-size: 18px;
  /* align-items: center;
  justify-content: center;
  justify-items: center; */
  font-weight: 600;
  /* margin: 3px; */
  margin-bottom: 20px;
  /* margin-left: 0px; */
}

.item.dragging {
  background-color: rgb(206, 68, 68);
}
.app-bg {
  margin: 0;
  padding: 0;
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  /* flex-direction: row; */
}
.container {
  display: flex;
  /* flex-direction: column; */
  height: 100vh;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  /* margin: 20px; */
}
.container-box {
  display: flex;
  height: 470px;
  /* width: 80%; */
  background: teal;
  padding: 20px;
  margin: auto;
  border-radius: 7px;

  box-shadow: 3px 5px 10px 3px #000000;
}
.header {
  color: white;
  font-size: 29px;
  margin-bottom: 10px;
  text-align: center;
  text-transform: uppercase;
}
.task-details {
  color: #414141;
  text-align: center;
  font-weight: 700;
  font-size: 17px;
}
.text-field {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 200px;
  justify-items: center;
  margin-left: auto;
  border-bottom: 3px solid #212121;
  padding: 5px;

}
.input-field {
  appearance: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgb(2, 53, 53);
  border: none;
  /* width: 30%; */
  color: white;
  margin: 10px;
  padding: 8px 20px;
  outline: none;

}
.input-field::placeholder {
  color: white !important;
  font-weight: 500;
  opacity: 1;
}
.btn {
  display: flex;
  flex-shrink: 0;
  background: rgb(206, 68, 68);
  border: 2px solid rgb(206, 68, 68);
  color: white;
  padding: 6px 25px;
  border-radius: 3px;
  outline: none;
  box-shadow: 0px 1px 2px 1px #212121;
}
.btn:hover {
  background: rgb(2, 53, 53);
  border: 2px solid rgb(2, 53, 53);
}
.task-list {
  /* border-bottom: 1px solid #212121; */
  border-left: 4px solid #212121;
  display: flex;
  justify-content: space-between;
  margin-bottom: 14px;
}
.todo-text {
  display: flex;
  flex-direction: column;
  font-size: 20px;
  align-items: center;
  justify-content: center;
  justify-items: center;
  font-weight: 600;
  margin: 4px;
}
.edit-btn {
  display: inline-block;
  background: #414141;
  border: 2px solid #414141;
  color: white;
  padding: 2px 5px;
  border-radius: 3px;
  cursor: pointer;
  width: 60px;
  margin: 4px;
}
.delete-btn {
  display: inline-block;
  background: #414141;
  border: 2px solid #414141;
  color: white;
  padding: 2px 5px;
  border-radius: 3px;
  cursor: pointer;
  width: 60px;
  margin: 4px;
}
